// Version sans Header/Footer pour éviter les erreurs de contexte
import React from "react";
import { useParams, Link } from "react-router-dom";
import { bigProjects } from "../../portfolio";

export default function ProjectDetails() {
    const { projectId } = useParams();

    console.log("ProjectID:", projectId);
    console.log("BigProjects:", bigProjects);

    // Trouver le projet correspondant
    const project = bigProjects.projects.find(
        (p, index) => index.toString() === projectId
    );

    console.log("Project found:", project);

    if (!project) {
        return (
            <div style={{
                minHeight: "100vh",
                padding: "2rem",
                textAlign: "center",
                backgroundColor: "#f8f9fa"
            }}>
                <h1 style={{ color: "#333", marginBottom: "1rem" }}>Projet non trouvé</h1>
                <p style={{ color: "#666", marginBottom: "2rem" }}>ProjectID: {projectId}</p>
                <Link
                    to="/"
                    style={{
                        color: "#55198b",
                        textDecoration: "none",
                        padding: "10px 20px",
                        border: "2px solid #55198b",
                        borderRadius: "5px",
                        display: "inline-block"
                    }}
                >
                    ← Retour à l'accueil
                </Link>
            </div>
        );
    }

    return (
        <div style={{
            minHeight: "100vh",
            padding: "2rem",
            backgroundColor: "#f8f9fa"
        }}>
            {/* Header simple */}
            <div style={{
                maxWidth: "1200px",
                margin: "0 auto",
                marginBottom: "2rem"
            }}>
                <Link
                    to="/"
                    style={{
                        color: "#55198b",
                        textDecoration: "none",
                        fontSize: "16px",
                        fontWeight: "500",
                        marginBottom: "2rem",
                        display: "inline-block"
                    }}
                >
                    ← Retour aux projets
                </Link>

                <h1 style={{
                    fontSize: "3rem",
                    color: "#333",
                    marginBottom: "1rem",
                    textAlign: "center",
                    background: "linear-gradient(135deg, #55198b, #7c4dff)",
                    WebkitBackgroundClip: "text",
                    WebkitTextFillColor: "transparent"
                }}>
                    {project.projectName}
                </h1>

                <p style={{
                    fontSize: "1.2rem",
                    color: "#666",
                    marginBottom: "2rem",
                    textAlign: "center",
                    maxWidth: "600px",
                    margin: "0 auto 2rem"
                }}>
                    {project.projectDesc}
                </p>

                {/* Métadonnées */}
                <div style={{
                    display: "flex",
                    justifyContent: "center",
                    gap: "2rem",
                    flexWrap: "wrap",
                    marginBottom: "3rem",
                    padding: "1.5rem",
                    backgroundColor: "rgba(85, 25, 139, 0.05)",
                    borderRadius: "10px",
                    border: "1px solid rgba(85, 25, 139, 0.1)"
                }}>
                    <div style={{ textAlign: "center" }}>
                        <div style={{ fontSize: "0.9rem", color: "#55198b", fontWeight: "600" }}>
                            📅 Période
                        </div>
                        <div style={{ fontSize: "1rem", color: "#333", fontWeight: "500" }}>
                            {project.date || "Non spécifiée"}
                        </div>
                    </div>
                    <div style={{ textAlign: "center" }}>
                        <div style={{ fontSize: "0.9rem", color: "#55198b", fontWeight: "600" }}>
                            👥 Équipe
                        </div>
                        <div style={{ fontSize: "1rem", color: "#333", fontWeight: "500" }}>
                            {project.teamSize || 1} personne{(project.teamSize || 1) > 1 ? 's' : ''}
                        </div>
                    </div>
                    <div style={{ textAlign: "center" }}>
                        <div style={{ fontSize: "0.9rem", color: "#55198b", fontWeight: "600" }}>
                            ⏱️ Durée
                        </div>
                        <div style={{ fontSize: "1rem", color: "#333", fontWeight: "500" }}>
                            {project.duration || "Non spécifiée"}
                        </div>
                    </div>
                </div>

                {/* Contenu principal */}
                <div style={{
                    display: "grid",
                    gridTemplateColumns: window.innerWidth > 768 ? "1fr 1fr" : "1fr",
                    gap: "3rem",
                    backgroundColor: "white",
                    padding: "2rem",
                    borderRadius: "10px",
                    boxShadow: "0 4px 15px rgba(0,0,0,0.1)"
                }}>
                    {/* Image */}
                    <div>
                        <img
                            src={project.image}
                            alt={project.projectName}
                            style={{
                                width: "100%",
                                height: "auto",
                                borderRadius: "10px",
                                boxShadow: "0 10px 30px rgba(0, 0, 0, 0.1)"
                            }}
                        />
                    </div>

                    {/* Informations */}
                    <div>
                        <h2 style={{
                            color: "#333",
                            fontSize: "2rem",
                            borderBottom: "2px solid #55198b",
                            paddingBottom: "0.5rem",
                            marginBottom: "1rem"
                        }}>
                            À propos du projet
                        </h2>

                        <p style={{
                            fontSize: "1.1rem",
                            lineHeight: "1.7",
                            color: "#555",
                            marginBottom: "2rem",
                            textAlign: "justify"
                        }}>
                            {getProjectDescription(projectId)}
                        </p>

                        <h3 style={{ color: "#55198b", fontSize: "1.5rem", marginBottom: "1rem" }}>
                            Technologies utilisées
                        </h3>
                        <div style={{
                            display: "flex",
                            flexWrap: "wrap",
                            gap: "0.5rem",
                            marginBottom: "2rem"
                        }}>
                            {getProjectTech(projectId).map((tech, index) => (
                                <span
                                    key={index}
                                    style={{
                                        background: "linear-gradient(135deg, #55198b, #7c4dff)",
                                        color: "white",
                                        padding: "0.5rem 1rem",
                                        borderRadius: "20px",
                                        fontSize: "0.9rem",
                                        fontWeight: "500"
                                    }}
                                >
                  {tech}
                </span>
                            ))}
                        </div>

                        <h3 style={{ color: "#55198b", fontSize: "1.5rem", marginBottom: "1rem" }}>
                            Liens du projet
                        </h3>
                        <div style={{ display: "flex", gap: "1rem", flexWrap: "wrap" }}>
                            {project.footerLink.map((link, index) => (
                                <a
                                    key={index}
                                    href={link.url}
                                    target="_blank"
                                    rel="noopener noreferrer"
                                    style={{
                                        display: "inline-block",
                                        padding: "0.8rem 1.5rem",
                                        background: "linear-gradient(135deg, #55198b, #7c4dff)",
                                        color: "white",
                                        textDecoration: "none",
                                        borderRadius: "5px",
                                        fontWeight: "500",
                                        transition: "all 0.3s ease"
                                    }}
                                    onMouseOver={(e) => {
                                        e.target.style.transform = "translateY(-2px)";
                                        e.target.style.boxShadow = "0 5px 15px rgba(85, 25, 139, 0.3)";
                                    }}
                                    onMouseOut={(e) => {
                                        e.target.style.transform = "translateY(0)";
                                        e.target.style.boxShadow = "none";
                                    }}
                                >
                                    {link.name}
                                </a>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

// Fonctions helper
function getProjectDescription(projectId) {
    const descriptions = {
        "0": "VeryBadSplit est une application web innovante qui simplifie le partage d'additions et de frais entre amis. Inspirée par les difficultés rencontrées lors de sorties en groupe, cette application permet de diviser facilement les dépenses, de gérer les remboursements et de maintenir un historique transparent des transactions.",
        "1": "Scoleton est un site web de gestion académique développé dans le cadre de la SAE 3A. Ce projet vise à centraliser la gestion des étudiants, des cours et des notes dans une interface web moderne et intuitive. Le système permet aux enseignants de gérer leurs classes et aux étudiants de suivre leur progression académique en temps réel.",
        "2": "Ce TP de mathématiques explore l'implémentation de l'algorithme de descente de gradient à pas constant en Python. Le projet illustre les concepts fondamentaux de l'optimisation numérique et de l'apprentissage automatique, avec des visualisations interactives pour comprendre la convergence.",
        "3": "Symphonie Steps est un jeu vidéo musical créé lors de la Code Game Jam 2025 avec le moteur Godot. Ce jeu rythmique innovant combine musique et gameplay, où les joueurs doivent synchroniser leurs actions avec la bande sonore."
    };
    return descriptions[projectId] || "Description du projet à venir...";
}

function getProjectTech(projectId) {
    const technologies = {
        "0": ["React", "Node.js", "Express", "MongoDB", "CSS3", "JavaScript"],
        "1": ["PHP", "HTML/CSS", "JavaScript", "MySQL", "Bootstrap"],
        "2": ["Python", "NumPy", "Matplotlib", "Jupyter Notebook"],
        "3": ["Godot Engine", "GDScript", "Audio Engine", "2D Graphics"]
    };
    return technologies[projectId] || [];
}